.side-menu {
    height: 100vh;
    background: white;
    width: 50%;
    align-self: flex-end;
    z-index: 2;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.side-menu a {
    color: black;
    font-weight: bolder;
    font-size: 2rem;
    margin: 2rem 0;
}

.exit-icon {
    position: absolute;
    top: 30px;
    right: 30px;
    font-size: 2rem;
    cursor: pointer;
}


@media (max-width: 600px) {
    .side-menu {
        width: 100%;
    }
}