.navigation-container {

    width: 100%;
    display: flex;
    flex-direction: column;
}
.navigation-bar {
    height: 13vh;
    background: white;
    box-shadow: 2px 2px 10px gray;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    z-index: 1;
}

.navigation-bar h3 {
    font-size: 2rem;
}

.list a {
    color: black;
    margin-right: 4rem;
}

.navigation-bar button {
    background: none;
    border: 2px solid black;
    padding: 0.5rem 1rem;
    border-radius: 5px;
}

.links {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.menu-icon  {
    display: none;
    font-size: 2rem;
    font-weight: bolder;
    cursor: pointer;
}

@media (max-width: 900px) {
    .links {
        display: none;
    }
    .menu-icon {
        display: block;
    }
}