.home {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    background: white;
}

.nav {
    position: fixed;
    width: 100%;
    z-index: 2;
}

.content {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.introduction {
    padding: 0 5%;
    align-self: center;
}

.introduction h3 {
    font-size: 3rem;
    line-height: 3.5rem;
    margin: 0;
    margin-bottom: 1rem;
}

.introduction p {
    margin-bottom: 1rem;
}

.hire-me-button {
    background: none;
    margin: 1px solid black;
    padding: 0.6rem 2.5rem;
    border-radius: 5px;
    margin-bottom: 2rem;
}


.languages img {
    width: 50px;
    height: 50px;
    margin-right: 1rem;
}

.em {
    width: 200px;
    transform: scaleX(-1);
    margin: 0;
}

@media (max-width: 700px) {
    .introduction h3 {
        font-size: 2.5rem;
    }
    .introduction p {
        font-size: 0.8rem;
    }
    .hire-me-button {
        padding: 0.4rem 2rem;
    }
    .languages img {
        width: 40px;
        height: 40px;
        margin-right: 0.8rem;
    }
    .em {
        width: 180px;
    }
}

@media (max-width: 560px) {
    .introduction h3 {
        font-size: 2rem;
        line-height: 2rem;
    }
    .introduction p {
        font-size: 0.6rem;
    }
    .hire-me-button {
        padding: 0.2rem 1.5rem;
    }
    .languages img {
        width: 20px;
        height: 20px;
        margin-right: 0.8rem;
    }
    .em {
        width: 130px;
    }
}

@media (max-width: 370px) {
    .introduction h3 {
        font-size: 1.5rem;
        line-height: 2rem;
    }
    .introduction p {
        font-size: 0.4rem;
    }
    .hire-me-button {
        padding: 0.1rem 0.5rem;
    }
    .languages img {
        width: 12px;
        height: 12px;
        margin-right: 0.8rem;
    }
    .em {
        width: 100px;
    }
}
