.projects {
    padding: 5%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project img {
    width: 300px;
    margin-left: 1rem;
}

.projects h3 {
    font-size: 2rem;
}

.projects p {
    font-size: 1rem;
    margin-bottom: 3rem;
}

.project {
    width: 500px;
    height: 300px;
    display: flex;
    box-shadow: 2px 2px 10px grey;
    padding: 1rem;
    position: relative;
}

.project h3 {
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

.project p {
    font-size: 0.7rem;
}

.projects-container {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}


.about-project {
    display: flex;
    flex-direction: column;
}

.link {
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    width: 100px;
    border: 2px solid black;
    border-radius: 5px;
    position: absolute;
    bottom: 20px;  
}

@media (max-width: 600px) {
    .project {
        width: 400px;
    }

    .project h3 {
        font-size: 1.2rem;
    }

    .project p {
        font-size: 0.6rem;
    }
    .project img {
        width: 245px;
    }

    .link {
        width: 80px;
    }

    .link a {
        font-size: 0.7rem;
    }
}

@media (max-width: 400px) {
    .project {
        width: 300px;
        padding: 5px;
    }

    .project h3 {
        display: none;
    }

    .project p {
        display: none;
    }
    .project img {
        width: 290px;
        margin: 0;
    }

    .link {
        width: 80px;
        left: 10px;
        bottom: 10px;
        background: white;
    }

    .link a {
        font-size: 0.7rem;
    }
}